import React, { useEffect, useState, useRef } from "react";

import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, ObjectKeys } from "../../../stores/Utils";

import "./styles.css"
import { RegisterComprobante_Cmpt } from "..";
import { OpenModal } from "../../../components/DialogHRM";
import { getall_entries, delete_entryParam } from "../services/server_fetching";
import DeleteAlert from "./deleteAlert";
import { SvgIcon_Cmpt } from "../../../components";
import { useNavigate } from "react-router";

const RegisterComprobante = RegisterComprobante_Cmpt();

const Svg = SvgIcon_Cmpt();



const AccountItem  = ({ data, elmId, handleRefreshAll }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	const navigate = useNavigate();


	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			// let frm2 = {"firstName":"Belisario","lastName":"Gonzalez Arevalo","genre":"Male","dateOfBirth":"06/02/1976","placeOfBirth":{"city":"Holguin","state":"Holguin"},"currentAddress":{"No":"4307","street":"Estate Dr","city":"Louisville","state":"Kentucky","zipcode":"40216"},"dob":202536000000}
			//updStore(frmNm, frm2);
			set_obs(gen16CodeId())
		}
	});

	
	const updObs =  () => {
		set_obs(gen16CodeId())
	}

	

	const refreshAccs =  () => {
		getall_entries(updObs)
	}

	const goToComprob =  () => {
		navigate({
				pathname: "/accounting-ledger"
		});
		
	}





	

	const openEntryComprobante =  () => {
		let modaldata = {};
		modaldata["zIndex"] = 450;
		modaldata["height"] = "80vh";
		// data['observeResize']=true;
		modaldata["props"] = { minHeight: "1vh", overlay: true };
		modaldata["content"] = <RegisterComprobante confirm={goToComprob} item={data}/>
		
		OpenModal(modaldata);
	}

	const deleteComprobante =  () => {
		delete_entryParam(data?.accountingEntryId, handleRefreshAll)
	}
	

	const OpenDeleteAlert =  () => {
		let modaldata = {};
		modaldata["zIndex"] = 450;
		modaldata["height"] = "80vh";
		// data['observeResize']=true;
		modaldata["props"] = { minHeight: "1vh", overlay: true };
		modaldata["content"] = <DeleteAlert confirm={deleteComprobante} item={data}/>
		
		OpenModal(modaldata);
	}


	let subAcc = getStore("sub_account_group")?.[data.accountId];
	
	return (
		<div className="item_acc">
			<div className="_dsplFlx account_item_box account_header">
				<div className="_dsplFlx" >
					
					<div className="name">
						{data?. name}
					</div>
					<div className="code" >
						
					</div>
				</div>
				<div className="flexSpace"/>
				<div className=" addSbAcc purple" onClick={openEntryComprobante}>
					Lanzar Comprobante
				</div>
				<div className="flexSpace"/>
				<div className="icon" onClick={OpenDeleteAlert}>
					<Svg name={"outline_delete"} color={"#c62828"}/>
				</div>
			</div>
		</div>
	)
}
export default AccountItem;